"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import axiosInstance from "../../../ultis/axios";
import ResponsiveSidebar from "@/components/ResponsiveSidebar";

type Project = {
  _id: string;
  title: string;
  code: string;
};

export default function DashboardPage() {
  const router = useRouter();
  const [projects, setProjects] = useState<Project[] | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const token = typeof window !== 'undefined' ? (sessionStorage.getItem('token') || localStorage.getItem('token')) : null;
    if (!token) {
      router.replace('/login');
      return;
    }
    (async () => {
      try {
        // Assuming endpoint exists: /api/projects (adjust if different)
        const res = await axiosInstance.get('/api/projects');
        const list = Array.isArray(res.data) ? res.data : (res.data?.items || []);
        if (!list || list.length === 0) {
          router.replace('/no-project');
          return;
        }
        setProjects(list);
      } catch (e: any) {
        if (e?.response?.status === 404) {
          router.replace('/no-project');
          return;
        }
        setError(e?.response?.data?.message || 'Kh√¥ng th·ªÉ t·∫£i danh s√°ch d·ª± √°n');
      } finally {
        setLoading(false);
      }
    })();
  }, [router]);

  if (loading) return <main className="p-6">ƒêang t·∫£i...</main>;
  if (error) return <main className="p-6 text-red-600">{error}</main>;
  if (!projects) return null;

  return (
    <div className="min-h-screen bg-[var(--background)]">
      <ResponsiveSidebar />
      <main className="p-4 md:p-6 md:ml-64">
        <div className="mx-auto w-full max-w-7xl">
          <div className="mb-6 md:mb-8 flex flex-col gap-3 md:flex-row md:items-center md:justify-between">
            <div>
              <h1 className="text-2xl md:text-3xl font-semibold tracking-tight" style={{color:'var(--primary)'}}>D·ª± √°n</h1>
              <p className="text-sm opacity-70">Qu·∫£n l√Ω v√† truy c·∫≠p nhanh c√°c d·ª± √°n c·ªßa b·∫°n</p>
            </div>
            <div className="flex items-center gap-2">
              <button className="px-3 py-2 rounded-lg text-sm hover:bg-[var(--muted)]" style={{border:'1px solid var(--border)'}}>L·ªçc</button>
              <button onClick={() => router.push('/projects/new')} className="btn-primary rounded-lg px-4 py-2 shadow-sm hover:shadow transition-shadow">+ T·∫°o d·ª± √°n</button>
            </div>
          </div>
          <div className="grid grid-cols-1 sm:grid-cols-2 xl:grid-cols-3 gap-4 md:gap-6">
            {projects.map((p) => (
              <div key={p._id} className="card rounded-xl p-5 transition-shadow hover:shadow-sm">
                <div className="flex items-start justify-between gap-4 mb-3">
                  <div>
                    <div className="text-sm opacity-70">M√£: {p.code}</div>
                    <div className="text-lg font-semibold tracking-tight">{p.title}</div>
                  </div>
                  <div className="h-8 w-8 rounded-md flex items-center justify-center" style={{background:'var(--muted)'}}>üìÅ</div>
                </div>
                <div className="flex items-center gap-2">
                  <button onClick={() => router.push(`/projects/${p._id}`)} className="px-3 py-2 rounded-lg text-sm hover:bg-[var(--muted)]" style={{border:'1px solid var(--border)'}}>M·ªü</button>
                  <button onClick={() => router.push(`/projects/${p._id}`)} className="px-3 py-2 rounded-lg text-sm hover:bg-[var(--muted)]" style={{border:'1px solid var(--border)'}}>Chi ti·∫øt</button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </main>
    </div>
  );
}


